.bg_forestGreen_lightForestGreen {
	background: linear-gradient(15deg, 
	            	rgba(0, 102, 26, .97),
	            	rgba(0, 204, 51, .8),
	            	rgba(0, 102, 26, .97));
	background: linear-gradient(15deg, 
	            	rgba(0, 102, 26, .97),
	            	rgba(0, 204, 51, .8) 40% 60%,
	            	rgba(0, 102, 26, .97));
}
%ulStyle {
	margin: 0;
	padding: 0;
	list-style-type: none;
}
%noHighlightAndSelect {
	user-select: none;
	-webkit-tap-highlight-color: transparent;
}